import React, { Component } from 'react';
import { connect } from 'react-redux';
import AddCaption from './addCaption';
import SubmitPostField from './submitPost';
import SubmitReview from './submitReview';
import { submitPostData, onSitePopup, onsiteTokenUpdate } from '../../../../actions/themeActions'
import CloseButton from './Elements/closeButton';
import OnsiteLogo from './Elements/onSitelogo';
import AddUpload from './Elements/upload';
import Success from './success';
import { tb_onsite_btn_disabled } from '../../../../utils';
import io from "socket.io-client";
import { socket_client_url } from "../../../../actions/api";
import { CloudUrl } from '../../../../constants';
import "../../../../scss/uppyUploader.scss"
import { isEmbed, isSocialWall, isTagEmbed, isTagShop, onsiteRedirectUrl, FOR_TEST_SERVER } from '../../../../actions/api'
import getsnapUpLabels from './content'

const validateEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

const addMouseflowScript = () => {
    window._mfq = window._mfq || [];
    (function () {
        var mf = document.createElement("script");
        mf.type = "text/javascript";
        mf.async = true;
        mf.src = "//cdn.mouseflow.com/projects/5d24672d-901f-43be-86a6-a92685f165a6.js";
        document.getElementsByTagName("head")[0].appendChild(mf);
    })();

    window._mfq.push(["setVariable", "useProduct", "socialwalls.com"]);
    window._mfq.push(["setVariable", "UserType", "AIReviewWriter"]);
}



class OnSiteUpload extends Component {


    state = {
        submitPost: true,
        activeTab: 1,
        isOpen: true,
        step: 1,
        wall_id: null,
        name: null,

        lstName: null,
        orgnization: null,
        designation: null,

        email: null,
        upload_image: [{
            file: null,
            caption: null,
            review: null,
            rating: 0,
            error: false,
            activeTab: 1,
            type: 1,
            thumb_file: "",
            videoPath: "",
            isAiStatus: false
        }],
        receiveMail: false,
        tandc: true,

        orgnizationError: false,
        designationError: false,

        nameError: false,
        emailError: false,
        submitErrorMessage: null,
        apiRequest: false,
        isActiveIndex: 0,
        isAddMoreActive: false,
        isEdit: false,
        submitError: false,
        is_terms_check: 0,
        errorClickMore: false
    }

    handleNext = event => this.setState({ submitPost: false })
    hanleTab1 = event => this.setState({ activeTab: 1 })
    hanleTab2 = event => this.setState({ activeTab: 2 })

    componentDidMount() {
        const { wall } = this.props;
        addMouseflowScript()
        this.setState({ activeTab: wall.UgcSettings.choose_content_category == 3 ? 2 : 1 })
    }

    onShowCloseSubmitPicPopUp = event => {
        const { isOpen } = this.state;
        this.setState({ isOpen: !isOpen })

    }
    updateImageWithData = (data) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length < 20) {
            upload_image.push(data);
            this.setState({ upload_image })
        }
    }


    onClickToaddMore = event => {
        const { upload_image } = this.state;
        const { wall } = this.props;

        if (upload_image && upload_image.length < 20) {
            const activeTab = wall.UgcSettings.choose_content_category == 3 ? 2 : 1;
            upload_image.push({ file: null, caption: null, review: null, rating: 0, error: false, activeTab: activeTab, type: 1, thumb_file: "", videoPath: "" })
            this.setState({ isAddMoreActive: true, upload_image, isActiveIndex: upload_image.length == 0 || upload_image.length == 1 ? 0 : parseInt(upload_image.length) - 1, step: 1, activeTab: activeTab, submitError: false, errorClickMore: false })
        }
        else if (upload_image.length >= 20) this.setState({ errorClickMore: true })


    }

    onClickToUpdateMoreStep = (step) => event => {
        const { isAddMoreActive, isActiveIndex, upload_image } = this.state;
        if (isAddMoreActive && upload_image[isActiveIndex]) {
            upload_image.splice(isActiveIndex, 1);
        }
        this.setState({ isAddMoreActive: false, upload_image, step, isEdit: false, isActiveIndex: upload_image && upload_image.length > 0 && (parseInt(upload_image.length) - 1) > 0 ? (parseInt(upload_image.length) - 1) : 0, activeTab: 1, errorClickMore: false })
    }
    onClickToUpdateStep = (step) => event => {
        var isError = false;
        const { upload_image, isActiveIndex, activeTab } = this.state;
        if (upload_image && upload_image[isActiveIndex]) {
            if (activeTab == 1) {
                if (upload_image[isActiveIndex].caption == null || upload_image[isActiveIndex].caption == "" || (upload_image[isActiveIndex].caption).trim().length == 0) {
                    upload_image[isActiveIndex].error = true;
                    isError = true;
                }
                else upload_image[isActiveIndex].error = false

            }
            else if (activeTab == 2) {
                if (upload_image[isActiveIndex].review == null || upload_image[isActiveIndex].review == "" || upload_image[isActiveIndex].review.length == 0 || upload_image[isActiveIndex].rating == 0) {
                    upload_image[isActiveIndex].error = true;
                    isError = true;
                }
                else upload_image[isActiveIndex].error = false
            }

        }
        if (!isError) this.setState({ isAddMoreActive: false, upload_image, step, isEdit: false, activeTab: 1, submitErrorMessage: null, errorClickMore: false })
        else this.setState({ upload_image })
    }


    onSubmitPostData = event => {
        const { name, email, orgnization, designation, lstName, upload_image, is_terms_check, tandc, receiveMail } = this.state;
        const { wallId, modalPop, wall, feeds } = this.props;


        this.setState({ submitError: false })
        if (tandc && (name && name.length > 0) && (email && email.length > 0 && validateEmail(email) && !email.includes("mailinator")) && (upload_image && upload_image.length > 0) && tandc) {
            this.setState({ apiRequest: true })

            var ugcSuite = window.ugcSuite ? true : window.location.href.includes("ugcSuite=1") ? true : false
            var feedData = {}
            if (ugcSuite) {
                if (feeds && feeds.length > 0) {
                    feeds.map((fItem) => {
                        if (fItem && fItem.Network && Object.keys(fItem.Network).length > 0 && fItem.Network.id == 29 && fItem.value1) {
                            feedData = { feedId: fItem.value1 }
                        }
                    })
                }
            }

            var specificUserRequrementField = isSocialWall && [10000].includes(wall.Wall.owner) ? { orgnization, designation, name: `${name} ${lstName}` } : {}
            const postData = { wall_id: wall?.Wall.wallId ? wall?.Wall.wallId : wallId, name, email, upload_file: upload_image, receive_mail: receiveMail, onsite_token: modalPop.onsite_token, ...feedData, ...specificUserRequrementField }


            if ([104746].includes(wall.Wall.owner)) {
                postData.is_terms_check = is_terms_check
            }


            submitPostData(postData).then((response) => {
                const { onsite_token, message } = response.data;
                if (!window.location.href.includes('studio=1') && !wall.UgcSettings.onsite_moderation && message) {
                    const SOCKET_CONNECTION = io(`${socket_client_url}`, {
                        transports: ["websocket"],
                        transport: ["websocket"],
                        upgrade: true,
                        reconnection: true,
                        reconnectionDelay: 3000,
                        reconnectionDelayMax: 5000,
                        reconnectionAttempts: 2
                    });


                    var notify = new Array();
                    notify.push(wallId);
                    notify.push(message[0].synced);
                    SOCKET_CONNECTION.emit("poststatus", notify);
                    SOCKET_CONNECTION.on("reconnect_attempt", (attemptNumber) => {
                        console.log(`Reconnect attempt #${attemptNumber}`);
                        if (attemptNumber > 2) {
                            SOCKET_CONNECTION.disconnect(); // Stop reconnecting after 2 attempts
                        }
                    });
                }

                this.props.onsiteTokenUpdate(onsite_token)
                this.setState({ step: 3, apiRequest: false }, () => setTimeout(() => {
                    if (isSocialWall && !isEmbed) window.location.href = `${onsiteRedirectUrl}${wallId}${FOR_TEST_SERVER ? `?embed=true` : ``}`
                    this.props.onSitePopup(false)
                }
                    , 50000), document.querySelector('html').style.overflow = "auto")
            })

                .catch((error) => {
                    const { message } = error.toJSON();
                    const { data } = error.response;
                    if (message) {
                        this.setState({ submitError: true, submitErrorMessage: data?.resposne_message || message, apiRequest: false })
                    }
                    console.error(error);
                });
        }
        else {
            if (this.state.name == null || this.state.name.trim().length == 0) this.setState({ nameError: true, apiRequest: false })
            if ((this.state.orgnization == null || this.state.orgnization.trim().length == 0) && isSocialWall && [10000].includes(wall.Wall.owner)) this.setState({ orgnizationError: true, apiRequest: false })
            if ((this.state.designation == null || this.state.designation.trim().length == 0) && isSocialWall && [10000].includes(wall.Wall.owner)) this.setState({ designationError: true, apiRequest: false })

            if (this.state.email == null || this.state.email.trim().length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true, apiRequest: false })
            if (upload_image && upload_image.length == 0) this.setState({ submitError: true })

        }
    }



    addCaption = (activeIndex) => event => {
        const { upload_image } = this.state;

        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].caption = event.target.value;
            upload_image[activeIndex].review = "";
            upload_image[activeIndex].rating = 0;
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 1;
            upload_image[activeIndex].isAiStatus = false;



            this.setState({ upload_image })
        }
    }
    addReview = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].review = event.target.value;
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 2;
            upload_image[activeIndex].isAiStatus = false;
            this.setState({ upload_image })
        }
    }

    addAICaption = (activeIndex, txt) => {
        const { upload_image } = this.state;

        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].caption = txt;
            upload_image[activeIndex].review = "";
            upload_image[activeIndex].rating = 0;
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 1;
            upload_image[activeIndex].isAiStatus = true;



            this.setState({ upload_image })
        }
    }
    addAIReview = (activeIndex, txt) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].review = txt;
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 2;
            upload_image[activeIndex].isAiStatus = true;
            this.setState({ upload_image })
        }
    }



    addRatings = (rating, activeIndex) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].rating = rating;
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 2;
            upload_image[activeIndex].isAiStatus = false;
            this.setState({ upload_image })
        }
    }
    addPostImage = (file, thumb_file, videoPath, type, activeIndex, ext, size, fileName, mediaHeight, mediaWidth) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].file = file;
            upload_image[activeIndex].thumb_file = thumb_file;
            upload_image[activeIndex].videoPath = videoPath;
            upload_image[activeIndex].type = type;
            upload_image[activeIndex].ext = ext;
            upload_image[activeIndex].size = size;
            upload_image[activeIndex].fileName = fileName;
            upload_image[activeIndex].mediaHeight = mediaHeight;
            upload_image[activeIndex].mediaWidth = mediaWidth;
            this.setState({ upload_image })
        }
    }

    onClickToRemove = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            upload_image.splice(activeIndex, 1);
        }
        this.setState({ upload_image, errorClickMore: false })
    }


    onClickToImageRemove = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].file = null;
            upload_image[activeIndex].thumb_file = "";
            upload_image[activeIndex].videoPath = "";
            upload_image[activeIndex].type = 1;
            upload_image[activeIndex].ext = null;
            upload_image[activeIndex].size = 0;
            upload_image[activeIndex].fileName = null;
            upload_image[activeIndex].mediaHeight = 0;
            upload_image[activeIndex].mediaWidth = 0;
            this.setState({ upload_image })
            tb_onsite_btn_disabled(2)
        }
    }
    onClickToEdit = (activeIndex) => event => {
        const { upload_image } = this.state;
        let isActiveTab = 1;
        if (upload_image && upload_image[activeIndex]) {
            isActiveTab = upload_image[activeIndex].activeTab;
        }
        this.setState({ isActiveIndex: activeIndex, isEdit: true, step: 1, activeTab: isActiveTab })
    }
    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }

    onTextOrganazation = event => {
        this.setState({ orgnization: event.target.value, orgnizationError: false }, () => {
            if (this.state.orgnization == null || this.state.orgnization.length == 0) this.setState({ orgnizationError: true })
        })
    }


    onTextlstName = event => {
        this.setState({ lstName: event.target.value })
    }

    onTextDesignation = event => {
        this.setState({ designation: event.target.value, designationError: false }, () => {
            if (this.state.designation == null || this.state.designation.length == 0) this.setState({ designationError: true })
        })
    }
    onTextEmail = event => {
        this.setState({ email: event.target.value, emailError: false }, () => {
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
        })
    }

    onUpdatereceiveMail = event => {
        const { receiveMail } = this.state;
        this.setState({ receiveMail: !receiveMail })
    }
    onUpdatetandc = event => {
        const { tandc } = this.state;
        this.setState({ tandc: !tandc })
    }


    onClosePopUp = () => {
        const { wallId } = this.props;
        if (isSocialWall && !isEmbed) {
            window.location.href = `${onsiteRedirectUrl}${wallId}${FOR_TEST_SERVER ? `?embed=true` : ``}`
        }
        else {
            this.props.onSitePopup(false)
            document.querySelector('html').style.overflow = "auto";
        }

    }

    termsCheck = (event) => {
        this.setState({ is_terms_check: event })
    }

    render() {
        const { orgnization, designation, activeTab, lstName, isActiveIndex, step, orgnizationError, designationError, upload_image, isAddMoreActive, isEdit, name, email, receiveMail, tandc, nameError, emailError, apiRequest, submitError, submitErrorMessage, errorClickMore } = this.state;
        const { wall, modalPop } = this.props;
        const ErrorImage = { backgroundImage: `url(${CloudUrl}/media/images/error/nopost.png?v=2)` }

        const { onClosePopUp } = this;
        const isStatus = (wall && wall.UgcSettings && Object.keys(wall.UgcSettings).length > 0 && wall.UgcSettings.onsite_status)
        var defaultLabel = isStatus ? getsnapUpLabels(wall.UgcSettings.onsite_language) : getsnapUpLabels('en')
        return isStatus && defaultLabel ? <div className='onsite_submit_post_main' style={{ backgroundImage: wall.UgcSettings.onsite_background_image ? `url(${wall.UgcSettings.onsite_background_image})` : null, backgroundColor: wall.UgcSettings.onsite_background_color }}>
            <div className='tb_onsite_post_inner'>
                <div className='tb_onsite_upload_post'>
                    <div className='tb_onsite_box'>
                        <div className='tb_onsite_box_header'>
                            <CloseButton onClosePopUp={onClosePopUp} />
                            <OnsiteLogo wall={wall} />
                        </div>
                        {step == 1 ? <>
                            <div className='tb_onsite_box_title'>
                                <div className='tb_onsite_title'>{wall.UgcSettings.onsite_status ? wall.UgcSettings.onsite_popup_title : `Share Your Images With Us`}</div>
                                <div className='tb_onsite_sub_heading'>{wall.UgcSettings.onsite_status ? wall.UgcSettings.onsite_popup_sub_heading : `Upload your image featuring our brand products and get a chance to feature in our UGC lookbook. `}</div>
                            </div>

                            <div className='tb_onsite_box_body'>

                                {isTagShop || wall.UgcSettings.choose_content_category == 1 ?
                                    <div className='tb_onsite_post_tabs'>
                                        {wall.UgcSettings.choose_content_category == 1 ? < div className='tb_onsite_separator'>OR</div> : null}
                                        <div className='tb_onsite_tabs_nav'>
                                            {isTagShop || wall.UgcSettings.choose_content_category == 1 || wall.UgcSettings.choose_content_category == 2 ? <div className={`tb_onsite_tabs ${wall.UgcSettings.choose_content_category != 1 ? 'active-single-tab' : ''} ${activeTab == 1 || wall.UgcSettings.choose_content_category == 2 ? " active" : ""}`} style={wall.UgcSettings.choose_content_category == 2 ? { width: '100%' } : {}} onClick={this.hanleTab1}>{defaultLabel.captionTabT}</div> : null}
                                            {isTagShop || wall.UgcSettings.choose_content_category == 1 || wall.UgcSettings.choose_content_category == 3 ? <div className={`tb_onsite_tabs ${wall.UgcSettings.choose_content_category != 1 ? 'active-single-tab' : ''} ${activeTab == 2 || wall.UgcSettings.choose_content_category == 3 ? " active" : ""}`} style={wall.UgcSettings.choose_content_category == 3 ? { width: '100%' } : {}} onClick={this.hanleTab2}>{defaultLabel.reviewTabT}</div> : null}
                                        </div>
                                    </div>

                                    : null}
                                <div className='tb_onsite_tabs_content'>
                                    <AddUpload key={upload_image[isActiveIndex].thumb_file} updateImageWithData={this.updateImageWithData} isActiveIndex={isActiveIndex} addPostImage={this.addPostImage} item={upload_image[isActiveIndex]} onClickToImageRemove={this.onClickToImageRemove} onsite_token={modalPop.onsite_token} defaultLabel={defaultLabel} />
                                    {activeTab == 1 ?

                                        <AddCaption hasActive={this.handleNext} isActiveIndex={isActiveIndex} onClickToUpdateStep={this.onClickToUpdateStep} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} isAddMoreActive={isAddMoreActive} addCaption={this.addCaption} addAICaption={this.addAICaption} item={upload_image[isActiveIndex]} defaultLabel={defaultLabel} />
                                        :

                                        <SubmitReview isActiveIndex={isActiveIndex} onClickToUpdateStep={this.onClickToUpdateStep} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} isAddMoreActive={isAddMoreActive} addReview={this.addReview} addAIReview={this.addAIReview} item={upload_image[isActiveIndex]} addAICaption={this.addAICaption} addRatings={this.addRatings} addAIRatings={this.addRatings} defaultLabel={defaultLabel} />

                                    }
                                </div>
                            </div>
                        </> : step == 2 ? <SubmitPostField termsCheck={this.termsCheck} submitError={submitError} submitErrorMessage={submitErrorMessage} onClickToaddMore={this.onClickToaddMore} onSubmitPostData={this.onSubmitPostData} upload_image={upload_image} isEdit={isEdit} onClickToRemove={this.onClickToRemove} onTextName={this.onTextName} onTextEmail={this.onTextEmail} name={name} onTextDesignation={this.onTextDesignation} onTextOrganazation={this.onTextOrganazation} onTextlstName={this.onTextlstName} orgnizationError={orgnizationError} designationError={designationError} orgnization={orgnization} designation={designation} lstName={lstName} email={email} receiveMail={receiveMail} tandc={tandc} onUpdatereceiveMail={this.onUpdatereceiveMail} nameError={nameError} emailError={emailError} wall={wall} onClickToEdit={this.onClickToEdit} apiRequest={apiRequest} errorClickMore={errorClickMore} defaultLabel={defaultLabel} /> : step == 3 ? <Success wall={wall} onClosePopUp={onClosePopUp} /> : null}
                    </div>
                </div>
            </div>
        </div > :
            <div className="onsite_submit_post_main">
                <div className="tb_error_page wall_error">
                    <div className="tb_error_page_bg" style={ErrorImage}> </div>
                    <div className="tb_error_page_wrapper">
                        <div className="tb_error_content">
                            <div className="tb_error_title">Thanks for Your Efforts!</div>
                            <div className="tb_error_des">Our review collection campaign has come to an end. We appreciate your interest. 🙂</div>
                        </div>
                    </div>
                </div>
            </div>

    }
}
const mapStateToProps = state => {


    if (state.modalPop != undefined && state.modalPop.onSiteIsShowPopUp == true) {
        document.querySelector('html').style.overflow = "hidden";
    }

    return {
        modalPop: state.modalPop
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSitePopup: (status) => dispatch(onSitePopup(status)),
        onsiteTokenUpdate: (token) => dispatch(onsiteTokenUpdate(token)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(OnSiteUpload);
