        const { onClickToUpdateStep, isActiveIndex, isAddMoreActive, addCaption, item, onClickToUpdateMoreStep, defaultLabel } = this.props
        const { captionPH, captionErr, skipBtn, nextBtn } = defaultLabel;
        return (
            <div className='tb_onsite_add_caption_tab'>
                <WriteCaption {...this.props} rating={0} layout={1} />
                {/* <div className='tb_onsite_form_group'>
                    <textarea className="tb_onsite_form_control_textarea" rows="2" placeholder={captionPH} onChange={addCaption(isActiveIndex)} value={(item && item.caption) ? item.caption : ""} />
                    
                </div> */}
                {item.error ? <div className='tb_onsite_error'>* {captionErr}</div> : null}
                <div className='tb_onsite_form_actions' style={item && isAddMoreActive ? { justifyContent: 'space-between' } : null}>
                    {item && isAddMoreActive ? <div className='tb_onsite_btn_secondary' onClick={onClickToUpdateMoreStep(2)}>{skipBtn}</div> : null}
                    <div className='tb_onsite_btn_primary' id="tb_onsite_btn_id" onClick={onClickToUpdateStep(2)}>{nextBtn}</div>
                </div>
            </div>
        )
