import React, { Component } from 'react';
import ReactStars from "react-rating-stars-component";
import WriteCaption from './writeCaption';
class SubmitReview extends Component {
    ratingChanged = (newRating) => {
        const { isActiveIndex, addRatings } = this.props;
        addRatings(newRating, isActiveIndex)
    };
    render() {
        const { onClickToUpdateStep, isAddMoreActive, isActiveIndex, addReview, item, onClickToUpdateMoreStep, defaultLabel } = this.props;
        const { reviewPH, reviewErr, skipBtn, nextBtn } = defaultLabel;
        return (
            <div className='tb_onsite_add_caption_tab'>
                <WriteCaption {...this.props} rating={item && item.rating ? item.rating : 0} layout={2} />
                {/* <div className='tb_onsite_form_group'>
                    <textarea className="tb_onsite_form_control_textarea" rows="2" placeholder={reviewPH} onChange={addReview(isActiveIndex)} value={(item && item.review) ? item.review : ""} />
                    
                </div> */}
                {item.error ? <div className='tb_onsite_error'>* {reviewErr}</div> : null}
                <div className='tb_onsite_rating'>
                    <ReactStars size={24} count={5} onChange={this.ratingChanged} value={item && item.rating ? item.rating : 0} />
                </div>
                <div className='tb_onsite_form_actions' style={item && isAddMoreActive ? { justifyContent: 'space-between' } : null}>
                    {item && isAddMoreActive ? <div className='tb_onsite_btn_secondary' onClick={onClickToUpdateMoreStep(2)}>{skipBtn}</div> : null}
                    <div className='tb_onsite_btn_primary' id="tb_onsite_btn_id" onClick={onClickToUpdateStep(2)}>{nextBtn}</div>
                </div>
            </div>
        )
    }
}

export default SubmitReview;