import React, { Component } from 'react';
import OnSitePost from './Elements/onsitePost'
import { isSocialWall } from '../../../../actions/api';


class SubmitPostField extends Component {

    render() {
        const { submitError, termsCheck, submitErrorMessage, wall, onTextOrganazation, onTextDesignation, onClickToaddMore, onTextlstName, onSubmitPostData, upload_image, onClickToRemove, isEdit, orgnizationError, designationError, onTextName, onTextEmail, lstName, designation, orgnization, email, name, nameError, emailError, onClickToEdit, apiRequest, errorClickMore, defaultLabel } = this.props
        const { infoH, infoSubH, inputNL, inputNPH, inputNErr, inputEL, inputEPH, inputEErr, termsText, termsLT, onePostErr, detailErr, addMPBtn, submitBtn } = defaultLabel;
        return <>
            <div className='tb_onsite_box_title'>
                <div className='tb_onsite_title'>{infoH}</div>
                <div className='tb_onsite_sub_heading'><div className="tb_onsite_sup">*</div> {infoSubH}</div>
            </div>
            <div className='tb_onsite_box_body'>
                <div className='tb_onsite_tabs_content'>
                    {
                        (upload_image && upload_image.length > 0) ? <OnSitePost upload_image={upload_image} onClickToRemove={onClickToRemove} isEdit={isEdit} onClickToEdit={onClickToEdit} /> : null
                    }
                    <div className='tb_onsite_form'>
                        <div>

                            {isSocialWall && [10000].includes(wall.Wall.owner) ?
                                <>
                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>First Name<div className="tb_onsite_sup">*</div></div>
                                        <input type="text" className='tb_onsite_form_control' placeholder={'First Name'} onChange={onTextName} value={name} />
                                        {nameError ? <div className='tb_onsite_error'>{inputNErr}</div> : null}
                                    </div>

                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>Last Name</div>
                                        <input type="text" className='tb_onsite_form_control' placeholder={'Last Name'} onChange={onTextlstName} value={lstName} />
                                    </div>


                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>Organization<div className="tb_onsite_sup">*</div></div>
                                        <input type="text" className='tb_onsite_form_control' placeholder={'Organization'} onChange={onTextOrganazation} value={orgnization} />
                                        {orgnizationError ? <div className='tb_onsite_error'>{'Please enter your Organization'}</div> : null}
                                    </div>


                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>Designation<div className="tb_onsite_sup">*</div></div>
                                        <input type="text" className='tb_onsite_form_control' placeholder={'Designation'} onChange={onTextDesignation} value={designation} />
                                        {designationError ? <div className='tb_onsite_error'>{'Please enter your Designation'}</div> : null}
                                    </div>

                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>{inputEL}<div className="tb_onsite_sup">*</div></div>
                                        <input type="email" className='tb_onsite_form_control' placeholder={inputEPH} onChange={onTextEmail} value={email} />
                                        {emailError ? <div className='tb_onsite_error'>{inputEErr}</div> : null}
                                    </div>

                                </>

                                :

                                <>
                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>{inputNL}<div className="tb_onsite_sup">*</div></div>
                                        <input type="text" className='tb_onsite_form_control' placeholder={inputNPH} onChange={onTextName} value={name} />
                                        {nameError ? <div className='tb_onsite_error'>{inputNErr}</div> : null}
                                    </div>

                                    <div className='tb_onsite_form_group'>
                                        <div className='tb_onsite_form_label'>{inputEL}<div className="tb_onsite_sup">*</div></div>
                                        <input type="email" className='tb_onsite_form_control' placeholder={inputEPH} onChange={onTextEmail} value={email} />
                                        {emailError ? <div className='tb_onsite_error'>{inputEErr}</div> : null}
                                    </div>
                                </>
                            }


                            {/* <div className='tb_onsite_form_group'>
                                <label className='tb_onsite_checkbox'><input type="checkbox" checked={receiveMail} onClick={onUpdatereceiveMail} /><div className='tb_onsite_checkbox_ico'></div> I want to receive email updates.</label>
                            </div> */}

                            {[104746].includes(wall.Wall.owner) ?
                                <div className='tb_onsite_terms_condition'>
                                    <label style={{ display: 'flex' }}>
                                        <input onClick={(e) => termsCheck(e.target.checked ? 1 : 0)} className='display-flex' type="checkbox" />
                                        <span style={{ marginLeft: '5px' }}> I agree to CALM using my story for marketing purposes --</span>
                                    </label>
                                    <a style={{ color: '#1154cc', fontWeight: 'normal', fontSize: '11px', paddingLeft: '26px' }} href={wall?.UgcSettings?.onsite_terms_url || wall?.UgcSettings?.onsite_term_url} target="_blank"> click for more information on how your story will be used.</a>
                                </div>
                                :
                                <div className='tb_onsite_terms_condition'>{termsText}
                                    <a href={wall?.UgcSettings?.onsite_terms_url || wall?.UgcSettings?.onsite_term_url} target="_blank"> {termsLT} *</a></div>
                            }

                            {submitError ? <div className='tb_onsite_error'>* {submitErrorMessage ? submitErrorMessage : upload_image && upload_image.length == 0 ? onePostErr : detailErr}</div> : null}
                            <div className='tb_onsite_form_actions_footer'>
                                <div className='tb_onsite_btn_wrapper'>
                                    <div className='tb_onsite_btn_secondary' onClick={onClickToaddMore}>+ {addMPBtn}</div>
                                    <div className='tb_onsite_post_limits'><div className="tb_onsite_sup">*</div> Max 20 Posts</div>
                                </div>
                                <div className={`tb_onsite_btn_primary${apiRequest ? ` tb_onsite_spinner` : ``}`} onClick={onSubmitPostData}>{wall.UgcSettings.onsite_popup_btn_txt}</div>
                            </div>
                            {errorClickMore ? <div className='tb_onsite_error'>* Max 20 posts are allowed.</div> : null}
                        </div>
                    </div>
                </div>
            </div>
        </>
    }
}

export default SubmitPostField;